<template>
  <div class="wishlist-page py-5">
    <div class="container">
      <div class="row mb-4">
        <div class="col-12">
          <h1 class="display-4">Your Wishlist</h1>
          <p class="lead" v-if="likedProducts.length > 0">
            You have {{ likedProducts.length }} liked items
          </p>
          <p class="lead" v-else>
            Your wishlist is empty. Start adding products you love!
          </p>
        </div>
      </div>
      
      <div v-if="likedProducts.length > 0" class="row">
        <div 
          class="col-md-4 col-lg-3 mb-4 fade-in" 
          v-for="(product, index) in likedProducts" 
          :key="index"
        >
          <ProductCard 
            :product="product" 
            @add-to-cart="addToCart"
          />
        </div>
      </div>
      
      <div v-else class="text-center py-5">
        <i class="far fa-heart fa-4x text-muted mb-4"></i>
        <h3>No liked products yet</h3>
        <p class="text-muted">Start exploring our products and add your favorites</p>
        <router-link to="/products" class="btn btn-primary mt-3">
          Browse Products
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'
import ProductCard from '../components/ProductCard.vue'

export default {
  components: {
    ProductCard
  },
  setup() {
    const store = useStore()
    
    const likedProducts = computed(() => store.getters.likedProducts)
    
    const addToCart = (product) => {
      store.dispatch('addToCart', product)
    }
    
    return {
      likedProducts,
      addToCart
    }
  }
}
</script>

<style scoped>
.wishlist-page {
  min-height: 70vh;
}
</style>